<template>
  <div class="people-record-details">
    <modal
      title="车辆通行档案"
      :visible="visible"
      @close="$emit('close')"
      width="46.51vw"
      height="38.385vw"
      :visibleMask="!isBindVisible"
    >
      <div class="modal-bg" slot="modalBg"></div>
      <div class="details">
        <a-tabs
          class="record-tabs people-record-tabs"
          :class="`people-record-${activeKey}`"
          v-model="activeKey"
          @change="callback"
        >
          <a-tab-pane key="1" tab="车辆信息">
            <details-vehicle-info v-show="isBind"></details-vehicle-info>
            <details-vehicle-list
              v-show="!isBind"
              @click="setBind"
            ></details-vehicle-list>
          </a-tab-pane>
          <a-tab-pane key="2" tab="预警处置">
            <details-warning></details-warning>
          </a-tab-pane>
          <a-tab-pane key="3" tab="物品处置">
            <details-goods></details-goods>
          </a-tab-pane>
        </a-tabs>
      </div>
    </modal>
    <details-bind :visible="isBindVisible" @click="setBind"></details-bind>
  </div>
</template>

<script>
import TriangleBox from "../../components/TriangleBox";
import Modal from "../../components/Modal";
import DetailsVehicleInfo from "./detailsVehicleInfo";
import DetailsVehicleList from "./detailsVehicleList";
import DetailsWarning from "./detailsWarning";
import DetailsGoods from "./detailsGoods";
import DetailsBind from "./detailsBind";

import moment from "moment";

export default {
  components: {
    TriangleBox,
    Modal,
    DetailsVehicleInfo,
    DetailsVehicleList,
    DetailsWarning,
    DetailsGoods,
    DetailsBind
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isBind: false,
      isBindVisible: false,
      activeKey: "1"
    };
  },
  methods: {
    moment,

    callback() {},

    setBind() {
      this.isBindVisible = !this.isBindVisible;
    }
  }
};
</script>

<style lang="less" scoped></style>
