<!--
 * @Description: 弹框内容-物品处置
 * @Autor: Zhongyu
 * @Date: 2021-05-12 11:13:42
 * @LastEditors: Zhongyu
 * @LastEditTime: 2021-05-20 16:10:28
-->
<template>
  <div class="details-goods">
    <div class="goods-info">
      <div class="goods-info-title">
        <a-tabs
          class="goods-tabs"
          :class="`goods-tabs-${activeKey}`"
          v-model="activeKey"
          @change="handleTabsChange"
        >
          <a-tab-pane tab="物品信息" key="1">
            <div class="warning-info-content or-so-scroll swiper-container">
              <div class="swiper-wrapper">
                <div
                  v-for="index in 10"
                  :key="index"
                  class="swiper-slide item"
                  :class="{ 'select-item': selectItem == index }"
                  @click="checkItem(index)"
                >
                  <div class="item-left">
                    <triangle-box :is-hover="false">
                      <div class="item-left-content">
                        <img src="../../assets/img/charts/img-car.png" />
                        <div class="item-lect-hover">
                          <div class="hover-item hover-left">详</div>
                          <div class="hover-item hover-right">解</div>
                        </div>
                      </div>
                    </triangle-box>
                  </div>
                  <div class="item-right">
                    <div class="right-li">
                      <img src="../../assets/img/img-time-icon.png" />
                      <div class="text-ellipsis">
                        2021.04.15 15:37:20
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </a-tab-pane>
          <a-tab-pane tab="相邻物品" key="2">
            <div class="warning-info-content or-so-scroll swiper-container">
              <div class="swiper-wrapper">
                <div
                  class="item"
                  :class="{ 'select-item': selectItem == index }"
                  v-for="index in 10"
                  :key="index"
                  @click="checkItem(index)"
                >
                  <div class="item-left">
                    <triangle-box :is-hover="false">
                      <div class="item-left-content">
                        <img src="../../assets/img/charts/img-car.png" />
                        <div class="item-lect-hover">
                          <div class="hover-item hover-left">详</div>
                          <div class="hover-item hover-right">绑</div>
                        </div>
                      </div>
                    </triangle-box>
                  </div>
                  <div class="item-right">
                    <div class="right-li">
                      <img src="../../assets/img/img-time-icon.png" />
                      <div class="text-ellipsis">
                        2021.04.15 15:37:20
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </a-tab-pane>
        </a-tabs>
      </div>
    </div>
    <div class="dispose-info">
      <div class="dispose-info-title">
        处置信息
      </div>
      <div class="dispose-info-content">
        <div class="content-form">
          <div class="form-item">
            <div class="lable">处置措施：</div>
            <a-select
              class="default-select dispose-select"
              disabled
              defaultValue="1"
            >
              <a-select-option value="1">
                蓝色
              </a-select-option>
            </a-select>
          </div>
          <div class="form-item">
            <div class="lable">处置时间：</div>
            <a-input
              class="default-input display-input"
              disabled
              value="2"
            ></a-input>
          </div>
          <div class="form-item form-item-top">
            <div class="lable">处置措施：</div>
            <a-select
              class="default-select dispose-select"
              disabled
              defaultValue="1"
            >
              <a-select-option value="1">
                蓝色
              </a-select-option>
            </a-select>
          </div>
          <div class="form-item form-item-top">
            <div class="lable">处置时间：</div>
            <a-date-picker
              format="YYYY-MM-DD hh:mm:ss"
              :value="moment('2021-04-20 20:21:04', 'YYYY-MM-DD hh:mm:ss')"
              disabled
              class="default-date-picker dispose-date-picker"
            />
          </div>
          <div class="form-item form-item-top align-items-top">
            <div class="lable">简要案情：</div>
            <a-textarea
              value="车辆于09年12月份报失，车辆通过检查站时，民警晋国核查该车辆，并将车辆扣押，已经
移交给当地分管警局部门。"
              disabled
              class="default-textarea"
              placeholder="Basic usage"
              :rows="4"
            />
          </div>
          <div class="form-item form-item-top align-items-top">
            <div class="lable">处置照片：</div>
            <div class="img-list">
              <div class="img-item" v-for="item in 10" :key="item">
                <img :src="img" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TriangleBox from "../../components/TriangleBox";

import Swiper from "swiper";
import "swiper/swiper.less";

import moment from "moment";
export default {
  components: {
    TriangleBox
  },
  data() {
    return {
      activeKey: "1",
      selectItem: 1,
      img: require("../../assets/img/charts/img-car.png")
    };
  },
  mounted() {
    var mySwiper = new Swiper(".swiper-container", {
      slidesPerView: "auto", // 一页显示2个
      loop: false // 循环模式选项
    });
  },
  methods: {
    moment,

    /**
     * 切换面板的回调
     * @param {*} activeKey tabs选择key
     * @return {*}
     */
    handleTabsChange() {
      this.selectItem = 1;
    },

    /**
     * 点击物品时间
     * @param {*} index 物品下标
     * @return {*}
     */
    checkItem(index) {
      console.log("======");
      this.selectItem = index;
    }
  }
};
</script>

<style lang="less" scoped></style>
